import { Grid, MenuItem, Select, Stack, TextField } from "@mui/material";
import { bindActionCreators } from "redux";
import { setTitle } from "../../../redux/actions/theme/themeActions";
import { useCallback, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  getAdminOrgList,
  getAdminOrgByKeyWord,
  deleteOrg,
  dataMigration,
} from "../../../redux/actions/Admin/AdminActions";

import { makeStyles } from "@mui/styles";
import CustomButton from "../../../components/UI/CustomButton";
import { useNavigate } from "react-router-dom";
import { Search } from "@mui/icons-material";
import { debounce } from "lodash";
import config from "../../../config";
import DeleteConfirmation from "../../../components/UI/DeleteConfirmation";
import { DataGrid } from "@mui/x-data-grid";
import CancelToken from "../../../utils/cancelClass";
const ctc = new CancelToken();

const useStyle = makeStyles((theme) => ({
  aboutContainer: {
    paddingTop: "8em",
    width: "90%",
    textAlign: "center",
    margin: "auto",
    paddingBottom: "10em",
    [theme.breakpoints.down("md")]: {
      width: "95%",
    },
  },
  outlined: {
    borderRadius: "5px",
    border: "solid 1px #7dbaaf",
    fontSize: "12px",
    color: "#7dbaaf",
    fontWeight: 600,
    textTransform: "none",
  },
  contained: {
    borderRadius: "5px",
    backgroundColor: "#7dbaaf",
    fontSize: "12px",
    fontWeight: 600,
    color: "#fafafb",
    textTransform: "none",
  },
  text: {
    color: "#7dbaaf",
    fontSize: "12px",
    fontWeight: 600,
    textTransform: "none",
  },
  sortContainer: {
    display: "flex",
    justifyContent: "space-between",
    background: "white",
    padding: "10px",
    borderRadius: "8px",
  },
  sortLabel: {
    fontSize: "14px",
    color: "#696974",
    paddingTop: "10px",
    paddingRight: "10px",
  },
}));

const ListOrg = (props) => {
  const loc = useNavigate();

  const classes = useStyle();
  const dispatch = useDispatch();
  const actions = bindActionCreators(
    {
      setTitle,
      getAdminOrgList,
      getAdminOrgByKeyWord,
      deleteOrg,
      dataMigration,
    },
    dispatch
  );
  const [order, setOrder] = useState("de");
  const [loading, setLoading] = useState(true);
  const [orgListD, setOrgListD] = useState([]);
  const [keyword, setKeyword] = useState("");
  const [delId, setDelId] = useState("");
  const [deleteModal, setDeleteModal] = useState(false);
  const ud = useSelector((state) => state.auth.user);

  const handleDeleteModalOpen = (id) => {
    setDelId(() => id);
    setDeleteModal(() => true);
  };
  const handleDelete = async () => {
    setDeleteModal(false);
    setLoading(true);
    if (delId) {
      await actions.deleteOrg(delId);
      await getData();
      setDelId("");
    }
    setLoading(false);
  };

  const orgD = useSelector((state) => state.admin.orgList);
  const debouncedSave = useCallback(
    debounce((k) => getByName(k), 1000),
    []
  );
  const getData = async () => {
    setLoading(true);
    ctc.createToken();
    await actions.getAdminOrgList(ctc.getToken());
    setLoading(false);
  };

  const getByName = async (k) => {
    setLoading(true);
    ctc.createToken();
    await actions.getAdminOrgByKeyWord(k, ctc.getToken());
    setLoading(false);
  };

  useEffect(() => {
    actions.setTitle({ title: props.title });
    getData();
    return () => ctc.cancelTheApi();
  }, []);

  useEffect(() => {
    if (orgD) {
      setOrder("de");
      setOrgListD(() => orgD);
    }
  }, [orgD]);

  const handleOrd = (o) => {
    const d = [...orgD];

    if (o !== "de") {
      const f = o === 0 ? true : false;
      const fd = d.filter((a) => a.publish === f);
      setOrgListD(() => fd);
    } else {
      setOrgListD(() => orgD);
    }
    setOrder(o);
  };
  const handleKSearch = (k) => {
    debouncedSave(k);
    setKeyword(k);
  };
  // const uploadMigrationFile = async (file) => {
  //   var reg = /^.*\.xls[xm]?$/;
  //   if (file && file.name && reg.test(file.name)) {
  //     setLoading(true);
  //     let formData = new FormData();
  //     formData.append("data", file);
  //     await actions.dataMigration(formData);
  //     await getData();
  //     setLoading(false);
  //   } else {
  //     toast.error("Please Upload Valid XL File!");
  //   }
  // };
  const columns = [
    { field: "name", headerName: "Organization", flex: 1, minWidth: 120 },
    { field: "website", headerName: "Website", flex: 1, minWidth: 120 },
    // {
    //   field: "email",
    //   headerName: "Email",
    //   flex: 1,
    //   minWidth: 120,
    // },
    {
      field: "city",
      headerName: "City",
      width: 120,
    },
    {
      field: "state",
      headerName: "State",
    },
    {
      field: "zipcode",
      headerName: "Zip",
    },
    {
      field: "leadStatus",
      headerName: "Lead Status",
      width: 120,
      flex: 1,
    },
    {
      field: "publish",
      headerName: "Publish",
      sortable: false,
      width: 90,
    },
    {
      field: "action",
      headerName: "Action",
      sortable: false,
      filterable: false,
      width: 180,
      renderCell: (params) => {
        return (
          <div style={{ display: "flex", gap: "5px" }}>
            <CustomButton
              name={"Edit"}
              varient={"contained"}
              onclick={() => loc(`/org-edit/${params.row._id}`)}
            />
            {ud && ud.subRole && ud.subRole !== "analyst" && (
              <CustomButton
                name={"Delete"}
                varient={"contained"}
                styled={{ backgroundColor: "red" }}
                onclick={() => handleDeleteModalOpen(params.row._id)}
              />
            )}
          </div>
        );
      },
    },
  ];
  return (
    <div className={classes.aboutContainer}>
      <Grid container spacing={1}>
        {/* <Grid
          item
          lg={6}
          md={6}
          sm={6}
          xs={6}
          // order={{ md: 2 }}
          textAlign="center"
        >
          <CustomButton
            name="Add Search Link"
            className="w-100"
            varient={"contained"}
            onclick={() => loc("/add-search")}
          />
        </Grid> */}
        <Grid
          item
          lg={12}
          md={12}
          sm={12}
          xs={12}
          // order={{ md: 2 }}
          textAlign="center"
          mb={5}
        >
          <CustomButton
            name="Add Organization"
            className="w-100"
            varient={"contained"}
            onclick={() => loc("/org-add")}
          />
        </Grid>
      </Grid>
      <Grid container spacing={2}>
        <Grid
          item
          lg={12}
          md={12}
          sm={12}
          xs={12}
          // order={{ md: 2 }}
          textAlign="left"
        >
          <div>
            <Stack
              direction="row"
              justifyContent="space-between"
              flexWrap={"wrap"}
              gap="10px"
              style={{ marginBottom: "20px" }}
            >
              <div>
                <Search style={{ marginTop: "4px", marginRight: "3px" }} />
                <TextField
                  variant="standard"
                  placeholder="Search Organization"
                  value={keyword}
                  onChange={(e) => handleKSearch(e.target.value)}
                />
              </div>
              {/* <div style=  */}

              {/* <div style={{ display: "flex", gap: "5px" }}>
                <div style={{ fontSize: "18px", fontWeight: 450 }}>
                  Upload Data
                </div>
                <input
                  type="file"
                  value=""
                  multiple={false}
                  onChange={(e) => uploadMigrationFile(e.target.files[0])}
                />
              </div> */}
              <div className={classes.sortContainer}>
                <div className={classes.sortLabel}>View:</div>
                <Select
                  labelId="demo-simple-select-helper-label"
                  id="demo-simple-select-helper"
                  value={order}
                  size="small"
                  variant="standard"
                  onChange={(e) => handleOrd(e.target.value)}
                >
                  <MenuItem value="de">
                    <em>All</em>
                  </MenuItem>
                  <MenuItem value={0}>Published</MenuItem>
                  <MenuItem value={1}>Not Published</MenuItem>
                </Select>
              </div>
            </Stack>
            <div style={{ height: 550, overflow: "auto", width: "100%" }}>
              <DataGrid
                getRowId={(row) => row._id}
                rows={orgListD || []}
                disableExtendRowFullWidth={false}
                columns={columns}
                disableSelectionOnClick
                loading={loading}
                rowsPerPageOptions={[...config.pageSlot]}
              />
            </div>
          </div>
        </Grid>
      </Grid>
      <DeleteConfirmation
        open={deleteModal}
        title="Organization"
        handleClose={() => setDeleteModal(false)}
        handleDelete={() => handleDelete()}
      />
    </div>
  );
};
export default ListOrg;
